.jumbotron {
  background-color: var(--grey);
  min-height: 555px;
  padding: 20px 0;
  overflow: hidden;
}

.jumbotron-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}

.profile-image {
  width: 100%;
  height: 280px;
  object-fit: cover;
  object-position: center;
  background-color: var(--bg-image);
}

.profile-image img {
  width: 100%;
  height: 100%;
}

.profile-info h1,
.profile-info h3 {
  font-family: "Poppins", sans-serif;
}

.profile-info p {
  font-family: "Roboto", sans-serif;
  line-height: 22px;
  margin-bottom: 20px;
}

.profile-info > h1 {
  font-size: calc(32px + (45 - 32) * ((100vw - 300px) / (1600 - 300)));
  color: var(--primary);
  line-height: 36px;
  margin-bottom: 10px;
}
.profile-info > h3 {
  font-size: calc(28px + (36 - 28) * ((100vw - 300px) / (1600 - 300)));
  font-weight: 100;
  line-height: 36px;
  color: var(--tertiary);
}

.profile-action {
  display: flex;
  flex-direction: column;
}

.profile-action > .btn-email-me {
  margin-bottom: 10px;
}

.profile-info > .social-media {
  margin-top: 20px;
  text-align: center;
  list-style-type: none;
}

.social-media li {
  display: inline-block;
  margin-right: 15px;
}

.social-media li a {
  color: var(--primary);
  font-size: 28px;
}

@media screen and (min-width: 321px) {
  .profile-action {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }

  .profile-action > .btn-email-me {
    margin-right: 10px;
    margin-bottom: unset;
  }

  .profile-info > .social-media {
    text-align: unset;
  }
}

@media screen and (min-width: 426px) {
  .profile-image {
    height: 360px;
  }
}

@media screen and (min-width: 769px) {
  .profile-image {
    background-color: transparent;
  }

  .jumbotron {
    margin-top: 70px;
    padding: 0;
  }

  .jumbotron-container {
    display: flex;
    flex-basis: 1;
    flex-direction: row-reverse;
    justify-content: flex-end;
    align-items: center;
    overflow: hidden;
  }

  .profile-info {
    width: 55%;
  }

  .profile-image {
    width: 45%;
    max-width: 580px;
    height: 555px;
    position: relative;
    bottom: 0;
    right: 30px;
  }

  .profile-info > h1 {
    margin-top: 12px;
    margin-bottom: 15px;
  }

  .profile-info > .social-media {
    position: absolute;
    bottom: 20px;
  }
}
