.skip-to-content {
  background-color: var(--tertiary);
  color: var(--white);
  padding: 0 20px;
  line-height: 48px;
  text-decoration: none;
  position: absolute;
  left: 0;
  top: -75px;
}

.skip-to-content:focus {
  top: 0;
  z-index: 100;
}

.header-app {
  display: block;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 1;
}

.nav {
  background-color: white;
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 1px 4px rgba(50, 50, 50, 0.75);
}

.nav-container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-logo {
  min-width: 200px;
}
.nav-logo a {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 18px;
  text-decoration: none;
  color: var(--tertiary);
}

.nav-burger button {
  padding: 13px 15px;
  background: unset;
  border: unset;
  cursor: pointer;
}

.nav-mobile {
  visibility: hidden;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  transition: 0.2s ease;
  z-index: -5;
  opacity: 0;
  bottom: 0;
  left: 0;
  background-color: white;
}
.open {
  visibility: visible;
  z-index: initial;
  opacity: 100%;
}

.nav-mobile > .nav-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
}

.nav-menu {
  width: 100%;
}

.nav-item {
  line-height: 44px;
  width: 100%;
  text-align: center;
}

.nav-link {
  display: block;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-weight: 500;
  color: var(--tertiary);
}

.nav-link:hover {
  font-size: 20px;
  font-weight: bold;
  transition: 0.2s ease;
}

@media screen and (min-width: 769px) {
  .header-app {
    top: 0;
    bottom: unset;
  }

  .nav {
    height: 70px;
    /* box-shadow: 0px -1px 4px rgba(50, 50, 50, 0.75); */
    box-shadow: unset;
    /* background-color: var(--grey); */
  }

  .nav-logo a {
    font-size: 22px;
  }

  .nav-burger {
    display: none;
  }

  .nav-container > .nav-menu {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    list-style-type: none;
    max-width: 800px;
  }

  .nav-item {
    width: unset;
  }

  .nav-link {
    padding: 10px 15px;
  }

  .nav-link:hover {
    font-size: 18px;
    font-weight: 500;
  }

  .nav-link:hover::after {
    content: "";
    display: block;
    height: 2px;
    width: 100%;
    position: relative;
    top: 0;
    background-color: var(--tertiary);
    margin: 0 auto;
    transition: 0.2s ease;
  }
}
