.qualification {
  padding: 50px 0;
}

.qualification-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.qualification-image {
  width: 100%;
  max-height: 335px;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
  background-color: var(--bg-image);
}

.qualification-image img {
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 426px) {
  .qualification-items {
    display: grid;
    grid-template-columns: auto auto;
  }
}

@media screen and (min-width: 769px) {
  .qualification-container {
    flex-direction: row-reverse;
    align-items: center;
    gap: 25px;
  }

  .qualification-image {
    width: 40%;
    max-height: 485px;
  }

  .qualification-items {
    width: 60%;
  }
}
