:root {
  --primary: #006db6;
  --secondary: #1ba2fd;
  --tertiary: #003356;
  --grey: #f0f0f0;
  --white: #ffffff;
  --dark: #494949;
  --bg-image: rgba(0, 109, 182, 0.2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  width: 90%;
  height: 100%;
  margin: 0 auto;
}
