.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  font-size: 14px;
  border-radius: unset;
  cursor: pointer;
}

.btn-icn {
  width: 22px;
  height: 18px;
  margin-right: 5px;
  float: left;
}

.btn-icn svg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.btn-primary {
  border: 1px solid var(--primary);
  background-color: var(--primary);
  color: var(--white);
}
.btn-primary .btn-icn svg {
  fill: var(--white);
}
.btn-primary:hover {
  background-color: var(--tertiary);
  border: 1px solid var(--tertiary);
}

.btn-outline-primary {
  border: 1px solid var(--primary);
  color: var(--primary);
}
.btn-outline-primary .btn-icn svg {
  fill: var(--primary);
}
.btn-outline-primary:hover {
  border: 1px solid var(--primary);
  background-color: var(--primary);
  color: var(--white);
}
.btn-outline-primary:hover .btn-icn svg {
  fill: var(--white);
}
