.expertise {
  padding: 50px 0;
}

.expertise-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.expertise-image {
  width: 100%;
  max-height: 335px;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
  background-color: var(--bg-image);
}

.expertise-image img {
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 426px) {
  .expertise-items {
    display: grid;
    grid-template-columns: auto auto;
  }
}

@media screen and (min-width: 769px) {
  .expertise-container {
    flex-direction: row;
    align-items: center;
    gap: 25px;
  }

  .expertise-image {
    width: 40%;
    max-height: 485px;
  }

  .expertise-items {
    width: 60%;
  }
}
