footer {
  background-color: var(--primary);
  padding: 50px 0;
  margin-bottom: 50px;
  color: var(--white);
}

.footer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-left,
.footer-right {
  text-align: center;
}
.footer-left h1 {
  font-family: "Poppins", sans-serif;
}

.footer-right {
  margin-top: 5px;
}

.footer-left,
.footer-right {
  font-family: "Roboto", sans-serif;
}

.footer-left a,
.footer-right a {
  color: var(--white);
}

.footer-left > .social-media {
  margin-top: 15px;
}

.footer-left > .social-media li a {
  color: var(--white);
}

@media screen and (min-width: 769px) {
  footer {
    margin-bottom: unset;
  }
  .footer-container {
    flex-direction: row;
    justify-content: space-between;
  }

  .footer-right {
    text-align: right;
  }

  .footer-left {
    text-align: left;
  }
}
