.interest {
  background-color: var(--grey);
  padding: 40px 0;
  text-align: center;
}

.interest-container > h2 {
  font-family: "Poppins", sans-serif;
  font-size: calc(18px + (26 - 18) * ((100vw - 300px) / (1600 - 300)));
  color: var(--tertiary);
  margin-bottom: 15px;
}

.interest-action > .btn-projects-me,
.interest-action > .btn-email-me {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.interest-action > .btn-projects-me {
  margin-right: 10px;
  margin-bottom: 10px;
}

@media screen and (min-width: 426px) {
  .interest-action {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
  }

  .interest-action > .btn-projects-me,
  .interest-action > .btn-email-me {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .interest-action > .btn-projects-me {
    margin-right: unset;
    margin-bottom: unset;
  }
}

@media screen and (min-width: 769px) {
  .interest-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .interest-container > h2 {
    margin-bottom: unset;
  }

  .interest-action > .btn-projects-me,
  .interest-action > .btn-email-me {
    width: unset;
  }
}
